body {
    background-color: rgb(21 24 29);
    color: #eee;
}

.App {
    text-align: center;
    width: 100%;
}

.chaBox {
    background-color: #1c1f26;

    width: 700px;
    margin: 30px auto;
    padding: 30px 0;
    box-shadow: 4px 6px 11px -5px #000000;

    border: solid 1px;
    border-radius: 15px;
    border-color: #2b313a;
}

.nameof {
    font-weight: bold;
    font-size: 2rem;
}

.chaImg {
    width: 300px;
    border: solid 1px;
    border-radius: 15px;
    border-color: #2b313a;
    margin-top: 5px;
}

.oneJNume {
    font-weight: bold;
}

.equBox {
    background-color: #1c1f26;

    margin: 50px 0;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    box-shadow: 4px 6px 11px -5px #000000;

    width: 700px;
    height: 300px;
    margin: 0 auto 1000px;
    justify-content: center;
    align-items: center;
    border: solid 1px;
    border-radius: 15px;
    border-color: #2b313a;
}

.itemBoxSmall {
    display: flex;
    flex-direction: row;
    height: 45px;
}

.itemBoxSmall > span,
.itemBoxSmall > img {
    display: block;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 45px;
    border: 1px;
    border-color: #2b313a;
}

@media all and (max-width: 768px) {
    .chaBox {
        width: 500px;
    }

    .equBox {
        width: 500px;
    }
}
